import React, { Component } from "react";
import style from "./Tabs.module.css";
import NextDay from "../NextDay/NextDay";
import NowDay from "../NowDay/NowDay";


export default class Tabs extends Component {
  state = {
    oneDay: true,
    tenDays: false
  };

  oneDay = () => this.setState({ oneDay: true, tenDays: false });
  tenDays = () => this.setState({ oneDay: false, tenDays: true });


  render() {
    const { oneDay, tenDays } = this.state;
    const {text, iconUrl, temp_c, pressure_mb, humidity, maxtemp_c, mintemp_c, sunrise, sunset, wind_kph, forecastDays} = this.props;
    return(
      <>
        <div className={style.buttons}>
          <div className={style.flex}>
            <div onClick={this.oneDay}>
              <p className={style.nameToday}>TODAY</p>
            </div>
            <div onClick={this.tenDays}>
              <p className={style.nameToday}>LOOK AT THIS WEEK</p>
            </div>
          </div>

          {oneDay && <>
            <div className={style.fl}>
              <NowDay text={text} iconUrl={iconUrl} temp_c={temp_c} pressure_mb={pressure_mb} humidity={humidity} maxtemp_c={maxtemp_c} mintemp_c={mintemp_c} sunrise={sunrise} sunset={sunset} wind_kph={wind_kph} />
            </div>
           </>}
          {tenDays && <>
            <div>
              <NextDay key={"key"} forecastDays={forecastDays} />
            </div></>}
        </div>
      </>
    )
  }
}