import React, { Component } from "react";
import style from "./InputLocalityJs.module.css";

class InputLocality extends Component{

  state={
    time: new Date(),
  };

  componentDidMount() {
    this.update = setInterval(() => {
        this.setState({time:new Date()});
      }, 1000
    )}
  componentWillUnmount () {
    clearInterval(this.update)
  }



  render() {

    const {time} = this.state;

const {cityNameUpdate, name, country, localtime} = this.props;

    return  (
      <div className={style.locality}>
        <form onSubmit={cityNameUpdate}>
          <input className={style.inputLocal} type="text" name='city' placeholder="Input your locality"/>
        </form>
        <div className={style.cityCard}>
          <div className={style.wrap}>
            <div className={style.imgWrap}>
              <img width="50px" height="50px" src="https://image.flaticon.com/icons/svg/616/616450.svg" alt=""/>
            </div>
            <div className={style.textBlock}>
              <p>{name}, {country}</p>
            </div>
          </div>
          <p className={style.localTime}>{time.toLocaleString()}</p>
        </div>
      </div>

    )
}
}


export default InputLocality;
