import React, { Component } from 'react';
import axios from 'axios';
import style from './App.module.css';
import InputLocality from "./components/Inputlocality/InputLocalityJs";
import Tabs from "./components/Tabs/Tabs";
import Loader from "react-loader-spinner"
import Quote from "./components/Quote/Quote";

const WEATHER_KEY = '29db13c2e8994c13af183516192204';

class App extends Component {

  state ={
    isLoading: true,
    cityName: "kiev",
    numForecastDay: 8,
  };

  updateWeather() {
    const {cityName, numForecastDay} = this.state;
    const URL = `https://api.apixu.com/v1/forecast.json?key=${WEATHER_KEY}&q=${cityName}&days=${numForecastDay}`;
    axios.get(URL)
      .then(response => {
        return response.data;
      })
      .then( data => {

        console.log(data);
        this.setState({
          isLoading: false,
          cityName: data.location.name,
          temp_c: data.current.temp_c,
          text: data.current.condition.text,
          iconUrl: data.current.condition.icon,
          forecastDays: data.forecast.forecastday,
          pressure_mb: data.current.pressure_mb,
          humidity: data.current.humidity,
          maxtemp_c: data.forecast.forecastday[0].day.maxtemp_c,
          mintemp_c: data.forecast.forecastday[0].day.mintemp_c,
          sunrise: data.forecast.forecastday[0].astro.sunrise,
          sunset: data.forecast.forecastday[0].astro.sunset,
          wind_kph: data.current.wind_kph,
          name: data.location.name,
          country: data.location.country,
          localtime: data.location.localtime
        })
      })

      .catch(err => {
        if (err) console.error("Cannot fetch Weather Data from API", err);
      })
  }

  componentDidMount() {
    this.updateWeather();
  }

  cityNameUpdate = e => {
    e.preventDefault();
    this.setState({
      cityName: e.target.city.value
    }, () => this.updateWeather())
  };

  render() {
    const {isLoading, temp_c, text, iconUrl, forecastDays, pressure_mb, humidity, maxtemp_c, mintemp_c, sunrise, sunset, wind_kph, name, country, localtime, bgIMG} = this.state;
    return (
      <div className={style.App}>
        {isLoading ? <div className={style.loadBackground}>

          <div className={style.wrapLoader}>
            <h2  className={style.loading}>Loading</h2>
            <Loader
            type="Circles" color="mediumpurple" height={80} width={80} margin="100px"
          /> </div>
        </div>: (
            <div className={style.mainWrap}>
              <div className={style.flex} style={{backgroundImage: `url(${bgIMG})`}}>
                <InputLocality localtime={localtime} name={name} country={country} cityNameUpdate={this.cityNameUpdate} />
                <Tabs text={text} iconUrl={iconUrl} forecastDays={forecastDays} temp_c={temp_c} pressure_mb={pressure_mb} humidity={humidity} maxtemp_c={maxtemp_c} mintemp_c={mintemp_c} sunrise={sunrise} sunset={sunset} wind_kph={wind_kph}/>
              </div>
              <Quote/>
            </div>
        )}

      </div>
    );
  }
}

export default App;
