            <div className={style.blockFlex}>

              <div className={style.left}>
                <p className={style.conditionText}>{item.day.condition.text}</p>
                <img className={style.conditionIcon} src={item.day.condition.icon} alt=""/>
                <p className={style.temp_c}>Max t {item.day.maxtemp_c} C</p>
                <p className={style.temp_c}>Min t {item.day.mintemp_c} C</p>
              </div>

              <div className={style.right}>
                <p>Humidity: {item.day.avghumidity} %</p>
                <p>Wind: {item.day.maxwind_kph}</p>
              </div>

            </div>
